import request from '@/utils/request'

/* 任务列表 */
export const getTaskList = data => request({ url: '/task/listTask', method: 'post', data })
/* 台账列表 */
export const getLedgerList = data => request({ url: '/task/ledgerSelect', method: 'post', data })
/* 添加任务 */
export const insertTask = data => request({ url: '/task/insertTask', method: 'post', data })
/* 修改任务 */
export const updateTask = data => request({ url: '/task/updateTask', method: 'post', data })
/* 删除任务 */
export const deleteTask = data => request({ url: '/task/deleteTask', method: 'post', data })
/* 任务批次单个详情 */
export const getTaskById = data => request({ url: '/task/getTaskById', method: 'post', data })
/* 下发人员列表  */
export const userList = data => request({ url: '/user/listUser', method: 'post', data })
/* 下发问题点  */
export const sharePoint = data => request({ url: '/checkPoint/sharePoint', method: 'post', data })
