<template>
  <div id="tablet">
    <div class="queryBox">
      <Button type="primary" size="small" icon="ios-arrow-back" class="sign-btn" @click="$emit('signOut',false)">返回</Button>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="spotsLoading" :columns="spotsColumns1" :data="spotsData1" highlight-row :max-height="tableHeight"></Table>
      </div>
    </div>
    <!-- 地图查看-->
    <div class="extract_box" :class="{spotsAddActive:spotsAddActive}">
      <taskManagementTableMap ref="taskManagementTableMap" @signOut="signOut"></taskManagementTableMap>
    </div>
  </div>
</template>

<script>
import taskManagementTableMap from './taskManagementTable_map'
import { renderDateTime } from '@/utils/common'
import {
  // 表格列表
  getTaskById
} from '@/api/taskManager'
export default {
  name: 'spotsTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: Math.floor(window.innerHeight - 280),
      /* 表格数据 */
      spotsColumns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '台账名称',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'name ',
          render: (h, params) => {
            return h('span', params.row.name)
          }
        },
        // {
        //   title: '台账年份',
        //   align: 'center',
        //   ellipsis: true,
        //   tooltip: true,
        //   minWidth: 200,
        //   key: 'year ',
        //   render: (h, params) => {
        //     return h('span', params.row.year)
        //   }
        // },
        {
          title: '台账备注',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 100,
          key: 'remark ',
          render: (h, params) => {
            return h('span', params.row.remark)
          }
        },
        {
          title: '创建时间',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 100,
          key: 'createDate ',
          render: (h, params) => {
            return h('span', renderDateTime(params.row.createDate))
          }
        },
        {
          title: '操作',
          key: 'address',
          width: 120,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              // 查看地图
              h('Button',
                {
                  props: {
                    type: 'success',
                    size: 'small',
                    icon: 'ios-map'
                  },
                  style: {
                    marginRight: '5px',
                    background: 'rgb(16,186,204)',
                    borderColor: 'rgb(16,186,204)'
                  },
                  on: {
                    click: () => {
                      this.spotsAddActive = true
                      this.$refs.taskManagementTableMap.initCom()
                    }
                  }
                }, '地图查看')
            ])
          }
        }
      ],
      spotsData1: [],
      /* 加载 */
      spotsLoading: false,
      /* 提取信息框状态 */
      spotsAddActive: false
    }
  },
  props: {
    /* 编辑行任务id */
    seeId: 0
  },
  components: {
    // 地图
    taskManagementTableMap
  },
  watch: {
    seeId () {
      this.dataId()
    }
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor(window.innerHeight - 280)
      this.getTableList()
    }, false)
  },
  methods: {
    dataId () {
      this.getTableList(this.seeId)
    },
    /* 获取字段列表 */
    getTableList (e) {
      this.spotsLoading = true
      getTaskById({
        data: {
          cl001: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.spotsData1 = res.data.data.data.rlhdGroupList
          this.spotsLoading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 信息提取地图页面退出 */
    signOut (spotsAddActive) {
      this.spotsAddActive = spotsAddActive
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    height: 100%;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      line-height: 50px;
      text-align: right;
    }
    .ml20 {
      margin-left: 20px;
    }
    .mr20 {
      margin-right: 20px;
    }
    .mt15 {
      margin-top: 15px;
    }
    .pull-right {
      float: right;
    }
    .btn-tabBtn {
      background: rgb(16,186,204);
      border-color: rgb(16,186,204);
      margin-right: 10px;
      &:hover {
        background: #13B1CC;
        border-color: #13B1CC;
      }
    }
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
      &.icon-leimupinleifenleileibie {
        font-size: 12px;
      }
    }
    .content_box {
      margin: 20px 15px;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .extract_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      bottom:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .spotsAddActive{
      right:0;
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .btn-import {
      background-color: rgb(194,146,1);
      border-color: rgb(194,146,1);
      &:hover, &:focus {
        background-color: #B28301;
        border-color: #B28301;
      }
    }
  }
</style>
