<template>
  <div id="taskEdit">
    <Modal
      v-model="taskEdit"
      title="修改核查任务">
      <Form ref="taskEditForm" :model="taskEditData" :rules="taskEditRule" :label-width="100" style="padding:0 20%;">
        <FormItem prop="cl003" label="ID：" style="display: none">
          <Input v-model.trim="taskEditData.cl001"/>
        </FormItem>
        <FormItem prop="cl002" label="任务名称：">
          <Input v-model.trim="taskEditData.cl002 " placeholder="请输入任务名称" size="default"/>
        </FormItem>
        <FormItem prop="cl010" label="任务年份：">
          <DatePicker size="default" type="year" :editable="false" v-model.trim="taskEditData.cl010" placement="bottom-end" placeholder="请选择任务年份" style="width: 100%"></DatePicker>
        </FormItem>
        <FormItem prop="ledgerIdList" label="任务台账：">
          <i-select v-model.trim="taskEditData.ledgerIdList" filterable style="width: 100%" placeholder="请选择任务台账">
            <i-option v-for="item in editLedgerList" :value="item.value">{{ item.label }}</i-option>
          </i-select>
        </FormItem>
        <FormItem prop="cl009" label="任务备注：">
          <i-input v-model.trim="taskEditData.cl009" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="请输入任务备注"></i-input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="taskEditSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { renderYearTime } from '@/utils/common'
import {
  // 台账列表
  getLedgerList,
  // 修改任务
  updateTask
} from '@/api/taskManager'
export default {
  name: 'Taskedit',
  data () {
    return {
      /* 数据表单校验 */
      taskEditRule: {
        cl002: [
          { required: true, message: '任务名称必须填写', trigger: 'blur' }
        ],
        ledgerIdList: [
          { required: true, message: '任务台账必须选择', trigger: 'change', type: 'number' }
        ]
      },
      /* 编辑框 */
      taskEdit: false,
      // 编辑数据
      taskEditData: {},
      // 任务台账
      editLedgerList: []
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  created () {
    this.getLedgerList()
  },
  methods: {
    // 获取查询条件中台账列表
    async getLedgerList () {
      await getLedgerList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          res.data.data.data.forEach((item, index) => {
            this.editLedgerList.push({ 'value': item.id, 'label': item.name })
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 编辑框默认值数据处理 */
    dataHandle () {
      this.taskEditData.cl001 = this.data.cl001
      this.taskEditData.cl002 = this.data.cl002
      this.taskEditData.cl010 = this.data.cl010
      this.taskEditData.cl009 = this.data.cl009
      let row = this.data.rlhdGroupList
      let arr1 = []
      if (row) {
        if (row.length > 0) {
          row.forEach((item, index) => {
            arr1.push(item.id)
          })
        }
      }
      // this.taskEditData.ledgerIdList = arr1
      this.$set(this.taskEditData, 'ledgerIdList', arr1[0])
    },
    /* 编辑核查任务 */
    taskEditSubmit () {
      this.$refs.taskEditForm.validate(valid => {
        if (valid) {
          let arr = []
          arr.push(this.taskEditData.ledgerIdList)
          updateTask({
            data: {
              cl001: this.taskEditData.cl001,
              cl002: this.taskEditData.cl002,
              cl010: renderYearTime(this.taskEditData.cl010),
              ledgerIdList: arr,
              cl009: this.taskEditData.cl009
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '修改核查任务成功' })
              this.$emit('getTableData')
              this.taskEdit = false
              this.taskEditData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.taskEditData = {
        cl003: '',
        cl002: '',
        cl010: '',
        ledgerIdList: '',
        cl009: ''
      }
      this.$refs.taskEditForm.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
